import React, {Component, Fragment} from 'react';
import { Responsive, Segment, Button, Icon, Menu, Grid, Container,Embed, Divider, Image } from 'semantic-ui-react';
import '../App.css';import { HashLink as Link } from 'react-router-hash-link';

const DataLabeling = (props)=>{
	const browserLang = props.browserLang;
	const spanish = browserLang.indexOf("es") > -1;
	const imgUrl = "images/background-image-hero.jpg";
	let backgroundImg = `linear-gradient(45deg,  rgba(0,26,255,.7) 1%,rgba(98,38,183,.7) 30%,rgba(140,40,99,.7) 69%,rgba(250,34,52,.7) 100%),url(${imgUrl})`;
	return(
		<header className="App-hero" style={{
			"minHeight":"80vh",
			"backgroundImage":backgroundImg,
			"color":"white"
		}}>
        
		<Segment basic style={{width:"70%","zIndex":"10"}}>
			<h1 style={{"textTransform": "uppercase"}}>
			{spanish ? (
				"Etiquetado de Datos"
				):
				"Data labeling"}
			
			</h1>
			<br/>
			<br/>
			<h2>{spanish ? 
				"Haga parte de la 4ta revolución industrial con Engin.ai, su aliado estratégico" : 
				"Enter the 4th industrial revolution with Engin.ai, your strategic partner"}</h2>
			<br/>
			<Link to="/#contact-us">
				<Button className="mainCTABtn" inverted color="white" size="huge" syle={{"width":"50%"}} >
				{spanish?
					<span className="buttonText">Contáctenos</span> 
					 : 
					<span>Contact Us</span>}
				<span className ="iconContainer" style={{"display":"inline-block", "width":"10px"}}>
					<Icon name="chevron right">
						
					</Icon>
				</span>
			</Button>

			</Link>
			
				
		</Segment>

      </header>
		);	
} 

export default DataLabeling;