import React, {Component} from "react";
import '../App.css';
import { HashRouter as Router , Switch, Route, withRouter, Redirect } from "react-router-dom";
import Homepage from "../Homepage";
import DataLabeling from "../components/DataLabeling";

class Main extends React.Component {
constructor(props){
		super(props);
		this.state = {};
	}
  render() {
    return (
      <Router>
      <Route
        path="/data-labeling"
         render={
          props=>(
            <DataLabeling browserLang={this.props.browserLang} {...this.props}></DataLabeling>
          )
         }
         >
         </Route>
        <Route
        exact path="/"
         render={
          props=>(
            <Homepage browserLang={this.props.browserLang} {...this.props}></Homepage>
          )
         }
         >
          
        </Route>
      </Router>
      
      );
  }
}

export default Main;