import React, {Component} from "react";
import {Form, Button} from 'semantic-ui-react';
import '../App.css';

class FormSpreeForm extends React.Component {
constructor(props){
		super(props);
		this.state = {};
	}
  render() {
    return (
      
        <Form style={{"display":"inline-block"}} size="huge" action="https://formspree.io/engin.ai.solutions@gmail.com" method="POST">
        	<Form.Group  widths='equal'>
	        	<Form.Field required>
	        		<Form.Input required name="name" type="email" placeholder='Email*' />
	        	</Form.Field>
        		
      			
      			<Form.Input name="name" placeholder='Company Name' />
        	</Form.Group>
        	<Form.Field required>
	        		<Form.TextArea rows={10} required name="name" placeholder='Comment*' />
	        	</Form.Field>
        	<Button size="huge" type="submit" className="CTA-2">Contact Us</Button>
        </Form>
     
    );
  }
}

export default FormSpreeForm;